body {
  background-color: #66fff7;
}

.App {
  background-color: #5313e9;
  min-height: 500px;
  width: 400px;
  margin: 40px auto;
}

#to-do-form input {
  background-color: #fff;
  border: 0;
  width: 200px;
  height: 50px;
  padding: 0 20px;
  margin: 20px;
  font-size: 18px;
  border-radius: 10px;
  color: rgb(223, 95, 95);
}

#to-do-form input ::placeholder {
  color: rgba(255, 255, 255, 0.5);
}

#to-do-form input :focus,
#to-do-form button :focus {
  outline: none;
}

#to-do-form button {
  height: 50px;
  width: 80px;
  border-radius: 5px;
  border: 0;
  background-color: #f32ec2;
  cursor: pointer;
  font-weight: bolder;
  color: #fff;
}
